import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import _ from 'lodash';

import { HttpRequestService } from '../../services/http-client.service';
import { SOURCES } from './../../constants/sources.constants';
import { CATEGORIES } from './../../constants/categories.constants';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent {
  newsList: any;
  sources: Object[];
  categories: Object[];
  source: any;
  searchQuery: String;
  subscriber: any;

  searchExpanded: Boolean;
  sourcesExpanded: Boolean;
  categoriesExpanded: Boolean;

  constructor(private httpClient: HttpRequestService,
              private route: ActivatedRoute) {
    this.categories = CATEGORIES;
    this.sources = SOURCES;

    this.searchQuery = '';

    this.searchExpanded = false;
    this.sourcesExpanded = false;
    this.categoriesExpanded = false;
  }

  ngOnInit() {
    this.subscriber = this.route.params.subscribe(params => {
      this.newsList = [];
      console.log('onInit');

      if (_.find(this.sources, {id: params['source']})) {
        this.sourcesExpanded = true;
        this.source = _.find(this.sources, {id: params['source']});

        this.httpClient.getNewsBySource(this.source.id)
          .then(result => {
            this.newsList = result.json().articles;
          });
      }

      if (_.find(this.categories, {id: params['category']})) {
        this.categoriesExpanded = true;
        this.source = _.find(this.categories, {id: params['category']});

        this.httpClient.getHeadlinesIn(this.source.id)
          .then(result => {
            console.log(result.json());
            this.newsList = result.json().articles;
          });
      }

      if (params['query']) {
        this.searchExpanded = true;
        this.source = {name: params['query']};
        this.searchQuery = this.source.name;

        this.httpClient.getNewsByQuery(this.source.name)
          .then(result => {
            this.newsList = result.json().articles;
          });
      }
    });
  }

  expandCollapseBlock(blockExpanded) {
    console.log(blockExpanded);
    blockExpanded = !blockExpanded;
    console.log(this.sourcesExpanded);
    return blockExpanded;
  }

}
